input[type="submit"],input[type="button"],button {
	text-align: center;
}

input[type="submit"],input[type="button"],input[type="reset"],button,input[type="text"],input[type="password"],input[type="date"],input[type="datetime"],input[type="datetime-local"],input[type="month"],input[type="week"],input[type="email"],input[type="number"],input[type="search"],input[type="tel"],input[type="time"],input[type="url"],textarea {
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	border-radius: 0;
	background-color: white;
	border: 1px solid #cccccc;
	color: #000;
	outline: 0px;
	padding: 4px;
}

input[type="text"]:focus,input[type="password"]:focus,input[type="date"]:focus,input[type="datetime"]:focus,input[type="datetime-local"]:focus,input[type="month"]:focus,input[type="week"]:focus,input[type="email"]:focus,input[type="number"]:focus,input[type="search"]:focus,input[type="tel"]:focus,input[type="time"]:focus,input[type="url"]:focus,textarea:focus {
	border-color: #999999;
	outline: none;
}

.button {
	text-transform: uppercase;
	font-size: 14px;
	display: inline-block;
	overflow: hidden;
    padding: 5px 16px;
    background: $brand-color;
    color: #fff;
    border-radius: 2px;
    transition: background;
    transition-duration: 0.2s;
    transform-style: ease-out;
    text-decoration: none;
    
    &:hover {
		background: #000;
    }
}

.searchform {
	input[type="submit"] {
		@extend .button;
		display: inline-block;
		vertical-align: middle;
		border: none;
	    padding: 7px 16px;
	    line-height: 1;
	}

	input[type="text"] {
		display: inline-block;
		vertical-align: middle;
	}
}